import React from 'react'
import Form from 'arui-feather/form'
import FormField from 'arui-feather/form-field'
import './CompanyGeneralDataForm.css'
import Label from 'arui-feather/label'
import Button from 'arui-feather/button'
import Heading from "arui-feather/heading";
import Paragraph from "arui-feather/paragraph";
import Sidebar from "arui-feather/sidebar";
import SelectTariffForm from "./SelectTariffForm";

class AccountsForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTariff: false,
      isSidebarVisible: false,
    };
    
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  render() {
    const {selectedTariff, isSidebarVisible} = this.state;
    return (
      <Form className="pb-3" onSubmit={this.handleSubmit}>
        <FormField className="bordered">
          <Label className="d-block mb-3">Тарифный пакет</Label>
          { selectedTariff && (
            <div className="d-flex justify-content-between align-items-center">
              <div className="left">
                <div className="font-bold">
                  <Heading size="s" className="mt-0">"Стандарт"</Heading>
                  <Label size="m" className="ml-2">0 <small className="opacity-middle">KZT</small> / 1 мес.</Label>
                </div>
              </div>
              <div className="right">
                <Button onClick={() => {this.setState({isSidebarVisible: true})}} view="extra">
                  Изменить пакет
                </Button>
              </div>
            </div>
          )}
          { !selectedTariff && (
            <div className="d-flex justify-content-between align-items-center">
              <div className="left">
                <div className="font-bold mt-2 opacity-middle">
                  <Paragraph>У вас нет выбранного пакета, выберите тарифный пакет, что бы продолжить</Paragraph>
                </div>
              </div>
              <div className="right">
                <Button onClick={() => {this.setState({isSidebarVisible: true})}}>
                  Выбрать пакет
                </Button>
              </div>
            </div>
          )}
        </FormField>
        <FormField className="bordered">
          <Label className="d-block mb-3">Текущий счет</Label>
          <div className="d-flex justify-content-between align-items-center">
            <div className="left">
              <div className="border_block px-5">
                <Label>USD</Label>
              </div>
            </div>
            <div className="right">
              <Button view="extra">
                Удалить
              </Button>
            </div>
          </div>
        </FormField>
        <FormField className="text-center pb-0">
          <Button view="action">Добавить счет</Button>
        </FormField>
        <Sidebar visible={isSidebarVisible} onCloserClick={ () => {this.setState({isSidebarVisible: false})} } width={768}>
          <SelectTariffForm onSelect={ () => {this.setState({selectedTariff: true, isSidebarVisible: false})} }/>
        </Sidebar>
        <div className="text-right mt-5">
          <Button view="extra" className="blued" type="submit">Далее</Button>
        </div>
      </Form>
    )
  }
  handleSubmit(e) {
    console.log(this.props);
    if (this.props.onSubmit) {
      this.props.onSubmit(e)
    }
  }
}

export default AccountsForm;
