function generateByRegex(regex, val, message = "", canBeEmpty=false) {
  const err = [];
  if (canBeEmpty && val==='') return err;
  if (!regex.test(val)) err.push(message);
  return err;
}

export function canBeEmptyVal(validator) {
  return function (v) {
    return validator(v, true);
  };
}

export function isRequired(val) {
  const err = [];
  if (val==='') err.push('Это поле является объязательным');
  return err;
}
export function isBin(val) {
  return generateByRegex(/^[0-9]{3} [0-9]{3} [0-9]{3} [0-9]{3}$/gs, val, 'Неверный формат БИН', true);
}
export function isPhone(val) {
  return generateByRegex(/^\+[0-9] \([0-9]{3}\) [0-9]{3}-[0-9]{2}-[0-9]{2}$/gs, val, 'Неверный телефон', true);
}
export function atLeastOneDigit(val, canBeEmpty=false) {
  return generateByRegex(/\d/gs, val, 'Должна присутсвовать как минимум одна цифра', canBeEmpty);
}

export function atLeastOneUpperCaseAndLowerCase(val, canBeEmpty=false) {
  return generateByRegex(/(?=.*[a-z])(?=.*[A-Z])/gs, val, 'Должны присутсвовать как минимум по одному символу верхнего и нижнего регистра', canBeEmpty);
}

export function atLeastOneSymbol(val, canBeEmpty=false) {
  return generateByRegex(/[-+_!@#$%^&*.,?]/gs, val, 'Должен присутвовать как минимум один символ из "-+_!@#$%^&*.,?"', canBeEmpty);
}
export function minLength8(val, canBeEmpty=false) {
  const err = [];
  if(canBeEmpty) return err;
  if (val.length < 8) err.push('Минимальная длина должна составлять 8 символов')
  return err;
}
export default {
  isRequired, isBin, isPhone, atLeastOneDigit, atLeastOneSymbol, atLeastOneUpperCaseAndLowerCase, minLength8
}
