import React from 'react'
import './CompanyGeneralDataForm.css'
import Form from 'arui-feather/form'
import FormField from 'arui-feather/form-field'
import Input from 'arui-feather/input'
import {isRequired} from '../../../_helpers/validators'
import Select from 'arui-feather/select'
import CalendarInput from 'arui-feather/calendar-input'
import CheckBox from 'arui-feather/checkbox'
import Label from 'arui-feather/label'
import oked_list from '../../../_datas/oked-data'
import IconButton from 'arui-feather/icon-button'
import PlusIcon from 'arui-feather/icon/action/add'
import TrashIcon from 'arui-feather/icon/action/delete'
import Paragraph from "arui-feather/paragraph";
import Button from "arui-feather/button";

const defaultFlags = {
  touched: false,
  dirty: false,
  valid: false,
  changed: false,
  focused: false
};

const currencies = [
  {value: 'KZT', text: '₸'},
  {value: 'RUB', text: '₽'},
  {value: 'USD', text: '$'},
  {value: 'EUR', text: '€'},
];
const defaultCurrency = currencies[0].value;
const countries = [
  {value: 'RU', text: 'Россия'},
  {value: 'KG', text: 'Кыргызстан'},
  {value: 'UZ', text: 'Узбекистан'},
  {value: 'US', text: 'США'},
];
const defaultCountry = 'Казахстан';

const turnoverOptions = [
  {value: 'mass', text: 'до 1 250 млн KZT'},
  {value: 'middle', text: 'от 1 250 млн KZT'},
  {value: 'corporate', text: 'более 9 млрд KZT'},
];


const economic_activity_options = [
  {text: 'Не выбрано', value: null},
  ...oked_list
  .filter(item => item.Code.length === 4)
  .sort((a,b) => (a.Code.localeCompare(b.Code)))
  .map(item => ({text: item.Code + ' - ' + item.NameRus.slice(0,100), value: item.Code,}))
];

const branch_of_national_economy_options = [
  {text: 'Не выбрано', value: null},
  ...oked_list
  .filter(item => item.Code.length === 2)
  .sort((a,b) => (a.Code.localeCompare(b.Code)))
  .map(item => ({text: item.Code + ' - ' + item.NameRus.slice(0,100), value: item.Code,}))
];

const cities = [
  { value: 'Astana', text: 'Нур-Султан' },
  { value: 'Almaty', text: 'Алматы' },
  {
    type: 'group',
    title: 'Акмолинская область',
    content: [
      { value: '3', text: 'Атбасар' },
      { value: '2', text: 'Боровое' },
      { value: '1', text: 'Кокшетау' },
    ]
  },
  {
    type: 'group',
    title: 'Актюбинская область',
    content: [
      { value: '4', text: 'Актобе' },
      { value: '5', text: 'Хромтау' },
    ]
  },
  {
    type: 'group',
    title: 'Мангистауская область',
    content: [
      { value: '6', text: 'Актау' }
    ]
  }
];
const regions = [
  { value: 'Almaty1', text: 'Алмалинский район' },
  { value: 'Almaty2', text: 'Бостандыкскый район' },
  { value: 'Almaty3', text: 'Медеуский район' },
  { value: 'Astana1', text: 'Сарыаркинский район' },
  { value: 'Astana2', text: 'Целиноградский район' },
];

class CompanyGeneralDataForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        secret_word: {
          validators: [isRequired],
          value: '',
        },
        average_turnover: {
          validators: [isRequired],
          value: null,
        },
        average_turnover_currency: {
          validators: [],
          value: defaultCurrency,
        },
        registered_country: {
          validators: [],
          value: defaultCountry,
        },
        action_country: {
          validators: [],
          value: defaultCountry,
        },
        additional_countries: [],
        full_name: {
          validators: [],
          value: 'ТОО "Ромашка"',
        },
        short_name: {
          validators: [],
          value: 'ТОО "Ромашка"',
        },
        english_name: {
          validators: [],
          value: 'Romashka',
        },
        registration_date: {
          validators: [],
          value: '01.12.2015',
        },
        reregistration_date: {
          validators: [],
          value: '15.12.2018',
        },
        compliance_control_agreement: {
          validators: [],
          value: false,
        },
        legal_form: {
          validators: [],
          value: 'Товарищество с ограниченной ответственностью',//'Товарищество с ограниченной ответственностью',
        },
        economic_sector: {
          validators: [],
          value: 'Негосударственные нефин. орг.',//'Негосударственные нефин. орг.',
        },
        forms_of_ownership: {
          validators: [],
          value: 'Частная собственность',
        },
        type_of_private_enterprise: {
          validators: [],
          value: 'Субъект среднего предпринимательства',
        },
        economic_activity: {
          validators: [],
          value: "0116",
        },
        additional_economic_activities: [],
        branch_of_national_economy: {
          validators: [],
          value: "01",
        },
        general_classifier_of_enterprises_and_organizations: {
          validators: [isRequired],
          value: '',
        },
        government_revenue_city: {
          validators: [isRequired],
          value: '',
        },
        government_revenue_region: {
          validators: [isRequired],
          value: '',
        },
      }
    };
    Object.keys(this.state.fields).forEach(field => {
      if (Array.isArray(this.state.fields[field])) {
        this.state.fields[field].forEach(item => item.flags = {...defaultFlags});
      } else {
        this.state.fields[field].flags = {...defaultFlags};
      }
      return field;
    });
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleArrayChange = this.handleArrayChange.bind(this);
    this.addFieldToArray = this.addFieldToArray.bind(this);
    this.removeFieldFromArray = this.removeFieldFromArray.bind(this);
  }
  render() {
    const {fields} = this.state;
    return (
      <Form className="pb-0" onSubmit={this.handleSubmit}>
        <Paragraph className="mb-3">
          <span className="red-star mx-2"/> - Поля отмеченные красной звездочкой обязательны для заполнения
        </Paragraph>
        <div className="row">
          <div className="col-md-6 pr-md-5">
            <FormField>
              <Input label="Кодовое слово"
                     width="available"
                     className="input-required"
                     value={fields.secret_word.value}
                     onChange={(v) => this.handleChange('secret_word', v)}
              />
            </FormField>
            <FormField>
              <Select label="Средний годовой оборот компании"
                      mode="radio"
                      width="available"
                      className="input-required"
                      value={fields.average_turnover.value ? [fields.average_turnover.value] : null}
                      onChange={(v) => this.handleChange('average_turnover', v[0])}
                      options={turnoverOptions}
              />
            </FormField>
            <FormField>
              <Input label="Страна регистрации"
                     width="available"
                     disabled
                     value={fields.registered_country.value}
              />
            </FormField>
            <FormField className={!fields.additional_countries.length ? '' : 'bordered'}>
              { !!fields.additional_countries.length && <Label className="d-block mb-2">Клиент осуществляет деятельность</Label>}
              <div className="d-flex align-items-end">
                <Input label={ !fields.additional_countries.length ? "Клиент осуществляет деятельность" : 'Основная страна' }
                       width="available"
                       disabled
                       className="input-required"
                       value={fields.action_country.value}
                />
                { !fields.additional_countries.length && <div className="button-accent"><IconButton onClick={() => this.addFieldToArray('additional_countries', {validators: [], value:""})} icon={<PlusIcon/>}/></div> }
              </div>
              {fields.additional_countries.map((field, index) => (
                <FormField key={index} className="mt-3 pb-0">
                  <div className="d-flex align-items-end">
                    <Select label="Доп. страна"
                            width="available"
                            mode="radio"
                            value={[field.value]}
                            options={countries}
                            onChange={(v) => this.handleArrayChange('additional_countries', index, v[0])}
                    />
                    <div className="button-accent"><IconButton onClick={() => this.removeFieldFromArray('additional_countries', index)} icon={<TrashIcon/>}/></div>
                    { index === fields.additional_countries.length-1 && field.value !== '' && <div className="button-accent"><IconButton onClick={() => this.addFieldToArray('additional_countries', {validators: [], value:""})} icon={<PlusIcon/>}/></div> }
                  </div>
                </FormField>
              ))}
            </FormField>
            <FormField>
              <Input label="Полное наименование"
                     width="available"
                     disabled
                     value={fields.full_name.value}
              />
            </FormField>
            <FormField>
              <Input label="Сокращенное наименование"
                     width="available"
                     disabled
                     value={fields.short_name.value}
              />
            </FormField>
            <FormField>
              <Input label="Наименование на английском"
                     width="available"
                     disabled
                     value={fields.english_name.value}
              />
            </FormField>
            <FormField>
              <CalendarInput label="Дата первичной регистрации"
                             width="available"
                             disabled
                             value={fields.registration_date.value}
              />
            </FormField>
            <FormField>
              <CalendarInput label="Дата последней перерегистрации"
                             width="available"
                             disabled
                             value={fields.reregistration_date.value}
              />
            </FormField>
          </div>
          <div className="col-md-6 pr-md-5">
            {/*<FormField>*/}
              {/*<Input label="ОКПО"*/}
                     {/*className="input-required"*/}
                     {/*width="available"*/}
                     {/*value={fields.general_classifier_of_enterprises_and_organizations.value}*/}
                     {/*onChange={(v) => this.handleChange('general_classifier_of_enterprises_and_organizations', v)}*/}
              {/*/>*/}
            {/*</FormField>*/}
            <FormField>
              <Label className="input-required d-block mb-2">Выбор органа государственных доходов</Label>
              <Select label="Область/Город"
                      mode="radio"
                      className="mb-3"
                      width="available"
                      options={cities}
                      value={[fields.government_revenue_city.value]}
                      onChange={(v) => this.handleChange('government_revenue_city', v[0])}
              />
              <Select label="Район"
                      mode="radio"
                      width="available"
                      options={regions}
                      value={[fields.government_revenue_region.value]}
                      onChange={(v) => this.handleChange('government_revenue_region', v[0])}
              />
            </FormField>
            <FormField>
              <Input label="Организационно-правовая форма"
                     width="available"
                     disabled
                     value={fields.legal_form.value}
              />
            </FormField>
            <FormField>
              <Input label="Сектор экономики"
                     width="available"
                     value={fields.economic_sector.value}
                     disabled
              />
            </FormField>
            <FormField>
              <Input label="Формы собственности"
                     width="available"
                     value={fields.forms_of_ownership.value}
                     disabled
              />
            </FormField>
            <FormField>
              <Input label="Тип частного предпринимательства"
                     width="available"
                     value={fields.type_of_private_enterprise.value}
                     disabled
              />
            </FormField>
            <FormField className={!fields.additional_economic_activities.length ? '' : 'bordered'}>
              <Label className="d-block mb-2">Вид экономической деятельности</Label>
              <div className="d-flex align-items-end">
                <div className="row w-100">
                  <div className="col-3">
                    <Input width="available"
                           label="Введите код ОКЭД"
                           value={fields.economic_activity.value ? fields.economic_activity.value+"" : ""}
                           disabled
                    />
                  </div>
                  <div className="col-9 pl-0">
                    <Input label="Расшифровка"
                           width="available"
                           value={fields.economic_activity.value}
                           disabled
                    />
                  </div>
                </div>
                { !fields.additional_economic_activities.length && <div className="button-accent"><IconButton onClick={() => this.addFieldToArray('additional_economic_activities', {validators: [], value:''})} icon={<PlusIcon/>}/></div>}
              </div>
              {fields.additional_economic_activities.map((field, index) => (
                <FormField key={index} className="mt-3 pb-0">
                  <Label className="d-block mb-2 opacity-middle" size="s">Дополнительный вид</Label>
                  <div className="d-flex align-items-end">
                    <div className="row w-100">
                      <div className="col-3">
                        <Input width="available"
                               label="код ОКЭД"
                               value={field.value ? field.value+"" : ""}
                               onChange={(v) => {this.handleArrayChange('additional_economic_activities', index, v)}}
                        />
                      </div>
                      <div className="col-9 pl-0">
                        <Select label="Расшифровка"
                                width="available"
                                value={[field.value]}
                                mode="radio"
                                options={economic_activity_options}
                                onChange={(v) => {this.handleArrayChange('additional_economic_activities', index, v[0])}}
                        />
                      </div>
                    </div>
                    { <div className="button-accent"><IconButton onClick={() => this.removeFieldFromArray('additional_economic_activities', index)} icon={<TrashIcon/>}/></div> }
                    { index === fields.additional_economic_activities.length-1 && field.value && field.value[0] && <div className="button-accent"><IconButton onClick={() => this.addFieldToArray('additional_economic_activities', {validators: [], value:''})} icon={<PlusIcon/>}/></div> }
                  </div>
                </FormField>
              ))}
            </FormField>
            <FormField>
              <Label className="d-block mb-2">Отрасль народного хозяйства</Label>
              <div className="row">
                <div className="col-3">
                  <Input width="available"
                         label="код ОКЭД"
                         value={fields.branch_of_national_economy.value ? fields.branch_of_national_economy.value+"" : ""}
                         disabled
                  />
                </div>
                <div className="col-9 pl-0">
                  <Select label="Расшифровка"
                          width="available"
                          value={[fields.branch_of_national_economy.value]}
                          options={branch_of_national_economy_options}
                          disabled
                  />
                </div>
              </div>
            </FormField>
            <FormField>
              <Label className="d-inline-block mt-4 mb-0 mr-3 input-required">Согласие на комплаенс контроль</Label>
              <CheckBox checked={fields.compliance_control_agreement.value}
                        onChange={(v) => this.handleChange('compliance_control_agreement', v)}
              />
            </FormField>
          </div>
        </div>
        <div className="text-right mt-5">
          <Button view="extra" className="blued" type="submit">Далее</Button>
        </div>
      </Form>
    )
  }
  handleChange(field, val) {
    console.log({field, val});
    const newState = {...this.state};
    newState.fields[field].flags.dirty = true;
    newState.fields[field].value  = val;
    this.setState(newState);
  }
  handleArrayChange(field, index, val) {
    console.log({field, index, val});
    const newState = {...this.state};
    newState.fields[field][index].flags.dirty = true;
    newState.fields[field][index].value  = val;
    this.setState(newState);
  }
  addFieldToArray(field, val) {
    console.log({field, val});
    const newState = {...this.state};
    val.flags = {...defaultFlags};
    newState.fields[field].push(val);
    this.setState(newState);
  }
  removeFieldFromArray(field, index) {
    console.log({field, index});
    const newState = {...this.state};
    newState.fields[field].splice(index, 1);
    this.setState(newState);
  }
  handleSubmit(e) {
    if (this.props.onSubmit) {
      this.props.onSubmit(e);
    }
  }
}

export default CompanyGeneralDataForm;
