import React from 'react'
import CompanyAddressForm from './CompanyAddressForm'
import CompanyContactsTable from './CompanyContactsTable'
import Button from 'arui-feather/button'
import Tabs from "arui-feather/tabs";
import TabItem from "arui-feather/tab-item";

class AddressContactTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'address'
    };
    this.handleTabChange = this.handleTabChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  render() {
    const {selectedTab} = this.state;
    const {canSubmit} = this.props;
    return (
      <div className="pb-5">
        <Tabs>
          <TabItem checked={ selectedTab === 'address' } onClick={(e) => this.handleTabChange(e,'address')}>
            Адрес
          </TabItem>
          <TabItem checked={ selectedTab === 'contacts' } onClick={(e) => this.handleTabChange(e,'contacts')}>
            Контакты
          </TabItem>
        </Tabs>
        { selectedTab==='address' && <CompanyAddressForm onSubmit={this.handleSubmit}/> }
        { selectedTab==='contacts' && <CompanyContactsTable onSubmit={this.handleSubmit}/> }
        {canSubmit && <div className="d-flex justify-content-end mt-5">
          <Button view="action" type="button">Отменить</Button>
          <Button view="extra" type="submit">Далее</Button>
        </div>
        }
      </div>
    )
  }

  handleTabChange(e, v) {
    e.preventDefault();
    this.setState({selectedTab: v})
  }
  
  handleSubmit(e) {
    if (this.props && this.props.onSubmit) {
      console.log('eee', e);
      this.props.onSubmit(e);
    }
  }

}

export default AddressContactTab;
