import React from 'react'
import {Table} from 'element-react'
import moment from 'moment'
import Button from 'arui-feather/button'
import TrashIcon from 'arui-feather/icon/action/delete'
import EditIcon from 'arui-feather/icon/action/edit'
import LupeIcon from 'arui-feather/icon/action/search'
import IconButton from 'arui-feather/icon-button'
import Modal from 'arui-private/modal'
import AddDocumentForm from './AddDocumentForm'
import Heading from 'arui-feather/heading'

const renderDateRow = (row, column) => {
  if (!row[column.prop]) return '';
  return moment(row[column.prop]).format('DD.MM.YYYY');
};

class CompanyDocumentsTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          label: "Вид документа",
          prop: "document_type",
          width: 200,
        },
        {
          label: "Рег. номер",
          prop: "registration_number",
          width: 160
        },
        {
          label: "Серия",
          prop: "serial",
          width: 100,
        },
        {
          label: "Номер",
          prop: "number",
          width: 100,
        },
        {
          label: "Действителен с",
          prop: "valid_from",
          render: renderDateRow,
        },
        {
          label: "Действителен по",
          prop: "valid_to",
          render: renderDateRow,
        },
        {
          label: "Действия",
          prop: "actions",
          width: 140,
          render: () => (
            <div className="d-flex align-items-center" style={{height: '60px'}}>
              <div className="button-accent"><IconButton icon={<LupeIcon size="m"/>}/></div>
              <div className="button-accent"><IconButton icon={<TrashIcon size="m"/>}/></div>
              <div className="button-accent"><IconButton icon={<EditIcon size="m"/>}/></div>
            </div>
          )
        },
      ],
      list: [
        {
          document_type: 'Справка о государственной регистрации ЮЛ',
          registration_number: '10100315435943',
          valid_from: new Date('02.27.2019'),
        }
      ],
      modal_visible: false,
    };
    this.handleClick = this.handleClick.bind(this);
  }
  render() {
    const {columns, list, modal_visible} = this.state;
    return (
      <div className="company-document-table">
        <div className="d-flex justify-content-end mb-4">
          <Button size="s" onClick={() => this.setState({modal_visible: true})}>Добавить</Button>
        </div>
        <Table className="w-100"
               columns={columns}
               data={list}
               emptyText="Список документов пуст, нажмите на кнопку «Добавить»"
               border/>
        <Modal visible={modal_visible}
               hasCloser
               autoclosable
               size="s"
               className="mb-5"
               onClose={() => this.setState({modal_visible: false})}>
          <Heading className="mt-0 mb-3" size='xs'>
            Регистрационные документы и сведения о лицензируемой деятельности
          </Heading>
          <AddDocumentForm onCancel={() => this.setState({modal_visible: false})}/>
        </Modal>
        <div className="text-right mt-5">
          <Button view="extra" className="blued" onClick={this.handleClick}>Далее</Button>
        </div>
      </div>
    );
  }
  
  handleClick(e) {
    if (this.props.onSubmit) {
      this.props.onSubmit(e);
    }
  }
}

export default CompanyDocumentsTable;
