import React from 'react'
import Form from 'arui-feather/form'
import FormField from 'arui-feather/form-field'
import Input from 'arui-feather/input'
import Button from 'arui-feather/button'
import Select from "arui-feather/select";
import {isRequired} from "../../../_helpers/validators";

const contact_type_options = [
  {value: 'Телефон', text: 'Телефон'},
  {value: 'Email', text: 'Email'},
];
const contact_view_options = [
  {value: 'Рабочий', text: 'Рабочий'},
  {value: 'Домашний', text: 'Домашний'},
  {value: 'Мобильный', text: 'Мобильный'},
  {value: 'Личный', text: 'Личный'},
];

class AddContactForm extends React.Component {
  constructor(props) {
    super(props);
    const {
      id = (new Date()).getTime(),
      contact_type = contact_type_options[0].value,
      contact_view = contact_view_options[0].value,
      description = '',
      contact_person = '',
    } = props;
    this.state = {
      fields: {
        id: {
          value: id,
        },
        contact_type: {
          validators: [isRequired],
          value: contact_type,
        },
        contact_view: {
          validators: [isRequired],
          value: contact_view,
        },
        description: {
          validators: [isRequired],
          value: description,
        },
        contact_person: {
          value: contact_person,
        },
      }
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  validateForm() {
    const newState = {...this.state};
    Object.keys(newState.fields).forEach(prop => {
      const field = {...newState.fields[prop]};
      field.errors = [];
      if (field.validators) {
        field.validators.forEach(validator => {
          if (field.value === undefined) return;
          field.errors.push(...validator(field.value));
        });
      }
      newState.fields[prop] = {...field};
    });
    this.setState(newState);
  }
  
  get isValid() {
    const errors = [];
    Object.keys(this.state.fields).forEach(prop => {
      const field = {...this.state.fields[prop]};
      if (field.validators) {
        field.validators.forEach(validator => {
          if (field.value === undefined) return;
          errors.push(...validator(field.value));
        });
      }
    });
    return errors.length === 0;
  }
  
  render() {
    const {contact_type, contact_view, description, contact_person} = this.state.fields;
    const {onCancel = () => {}} = this.props;
    const args = {};
    if (contact_type.value === 'Телефон') {
      args.mask='+7(7##)###-##-##';
      args.onFocus=() => this.handlePhoneFocus('description');
      args.onBlur=() => this.handlePhoneBlur('description');
    }
    return (
      <div className="mx-auto" style={{maxWidth: '400px'}}>
      <Form className="pb-2" onSubmit={this.handleSubmit}>
        <FormField>
          <Select label="Тип контакта"
                  mode="radio"
                  width="available"
                  value={[contact_type.value]}
                  options={contact_type_options}
                  onChange={v => this.handleChange('contact_type', v[0])}
                  className="input-required"/>
        </FormField>
        <FormField>
          <Select label="Вид контакта"
                  mode="radio"
                  width="available"
                  value={[contact_view.value]}
                  options={contact_view_options}
                  onChange={v => this.handleChange('contact_view', v[0])}
                  className="input-required"/>
        </FormField>
        <FormField>
          <Input label={`Введите ${contact_type.value.toLowerCase()}`}
                 width="available"
                 {...args}
                 value={description.value}
                 onChange={v => this.handleChange('description', v)}
                 className="input-required"/>
        </FormField>
        <FormField>
          <Input label="ФИО Контактного лица"
                 value={contact_person.value}
                 onChange={v => this.handleChange('contact_person', v)}
                 width="available"/>
        </FormField>
        <div className="d-flex justify-content-end">
          <Button view="action" type="button" onClick={onCancel}>Отменить</Button>
          <Button view="extra" type="submit" disabled={!this.isValid}>Сохранить</Button>
        </div>
      </Form>
      </div>
    )
  }
  
  handleChange(field, val) {
    console.log({field, val});
    const newState = {...this.state};
    newState.fields[field].value  = val;
    this.setState(newState);
  }
  
  handleSubmit(e) {
    if (this.isValid) {
      const fields = {...this.state.fields};
      Object.keys(fields).map(fieldName => fields[fieldName] = fields[fieldName].value);
      if (this.props.onSubmit) this.props.onSubmit(fields, e);
    }
  }
  
  handlePhoneFocus(field) {
    if (this.state.fields[field].value === '') {
      const newState = {...this.state};
      newState.fields[field].value = '+7(7';
      this.setState(newState);
    }
  }
  
  handlePhoneBlur(field) {
    if (this.state.fields[field].value.length < 5) {
      const newState = {...this.state};
      newState.fields[field].value = '';
      this.setState(newState);
    }
  }
}

export default AddContactForm;
