import React from 'react'
import Heading from 'arui-feather/heading'
import AddressForm from './AddressForm'
import FormField from "arui-feather/form-field";
import Label from "arui-feather/label";
import {Switch} from "element-react";
import Button from "arui-feather/button";

class CompanyAddressForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      similar: true,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  render() {
    const {similar} = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-12">
            <FormField className="mt-4 pb-1">
              <Label className="mr-3">Адрес ведения бизнеса совпадает с юридическим адресом?</Label>
              <Switch value={similar}
                      onText="Да"
                      onColor="rgba(11, 31, 53, 0.9)"
                      offText="Нет"
                      onChange={v => {this.setState({similar: v})}}
                      onValue={true}
                      offValue={false}
              />
            </FormField>
          </div>
          <div className="col-md-6 pr-md-5">
            <Heading size="s">Юридический адрес</Heading>
            <AddressForm/>
          </div>
          { !similar && (
            <div className="col-md-6 pr-md-5">
              <Heading size="s">Фактический адрес</Heading>
              <AddressForm/>
            </div>
          )}
        </div>
        <div className="text-right mt-5">
          <Button view="extra" className="blued" onClick={this.handleSubmit}>Далее</Button>
        </div>
      </div>
    )
  }
  
  handleSubmit(e) {
    console.log(this.props);
    if (this.props.onSubmit) {
      this.props.onSubmit(e)
    }
  }
}

export default CompanyAddressForm;
