import React from "react";
import Form from "arui-feather/form";
import FormField from "arui-feather/form-field";
import Label from "arui-feather/label";
import {Switch} from "element-react";
import Heading from "arui-feather/heading";
import Paragraph from "arui-feather/paragraph";
import Button from "arui-feather/button";
import addCompanyService from "../../../_services/addCompany.service";
import './SendDocumentsForm.css';
import RepeatIcon from 'arui-feather/icon/action/repeat';
import PdfIcon from 'arui-feather/icon/file/format-pdf';
import Spin from "arui-feather/spin";
import CheckBox from 'arui-feather/checkbox'
import Link from "arui-feather/link";
import Input from "arui-feather/input";

const full_type = "FULL";
const single_type = "SINGLE";
const DOS_STANDARD = 'DOS_STANDARD';
const DOS_NS = 'DOS_NS';
const DOS_NS_USA = 'DOS_NS_USA';

const iin = '970611300072';

class SendDocumentsForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      status: 'clean',
      fields: {
        iin,
        isNS: true,
        isUSA: false,
        type: single_type,
        frontPage: {
          file: undefined,
          src: '',
        },
        backPage: {
          file: undefined,
          src: '',
        },
        dosNsFile: {
          file: undefined,
          src: '',
        },
        dosNsUsaFile: {
          file: undefined,
          src: '',
        },
      },
      err: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  render() {
    const {status, fields, err} = this.state;
    return (
      <Form onSubmit={this.handleSubmit}>
        <FormField>
          <Label size="s" className="mr-3">Деятельность осуществляется по типовому уставу?</Label>
          <Switch value={fields.isNS}
                  onText="Да"
                  onColor="rgba(11, 31, 53, 0.9)"
                  offText="Нет"
                  onChange={v => this.handleChange('isNS', v)}
          />
        </FormField>
        <div className={`bordered ${status}`}>
          <FormField>
            <Input value={fields.iin} onChange={v => this.handleChange('iin',v)}/>
          </FormField>
          <FormField>
            <Heading size="s" className="bordered-heading">Документы</Heading>
            <Paragraph className="mb-0">
              Для формирования документов на подписание, загрузите основные документы:
            </Paragraph>
            <FormField className="mt-3 pb-0">
              <CheckBox checked={fields.type === full_type}
                        onChange={(v) => this.handleChange('type', v ? full_type : single_type) }
                        text="Обе стороны удостоверения личности находятся в одном файле"
              />
            </FormField>
            <ul className="row file-list">
              { fields.type===full_type && (
                <li className="mb-2 px-3 col-12">
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Изображение имеющее обе стороны удостоверения личности</Label>
                    <input type="file" id="file-1" name="uploadingFiles" accept="image/png, image/jpeg"  onChange={e => this.handleFileChange('frontPage', e)} style={{display: fields.frontPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-1">
                      {fields.frontPage.src && <div className="image-preview mt-2"><img src={fields.frontPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>
              )}
              { fields.type===single_type && ([
                <li className="mb-2 px-3 col-md-6" key={1}>
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Фотография лицевой части уд. личности</Label>
                    <input type="file" id="file-1" name="uploadingFiles" accept="image/png, image/jpeg"  onChange={e => this.handleFileChange('frontPage', e)} style={{display: fields.frontPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-1">
                      {fields.frontPage.src && <div className="image-preview mt-2"><img src={fields.frontPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>,
                <li className="mb-2 px-3 col-md-6" key={2}>
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Фотография тыльной части уд. личности</Label>
                    <input type="file" id="file-2" name="uploadingFiles" accept="image/png, image/jpeg" onChange={e => this.handleFileChange('backPage', e)} style={{display: fields.backPage.src ? 'none' : 'block'}}/>
                    <label htmlFor="file-2">
                      {fields.backPage.src && <div className="image-preview mt-2"><img src={fields.backPage.src} alt=""/><RepeatIcon size="l" className="repeat-icon" theme="alfa-on-color"/></div> }
                    </label>
                  </div>
                </li>
              ])}
            </ul>
            <ul className="row file-list">
              { !fields.isNS && (
                <li className="mb-2 px-3 col-md-12">
                  <div className="file-block">
                    <Label size="s" className="d-inline-block mb-2">Документ, который нужен для не типового устава</Label>
                    <input type="file" id="file-3" name="uploadingFiles" accept="application/pdf" onChange={e => this.handleFileChange('dosNsFile', e)} style={{display: fields.dosNsFile.file ? 'none' : 'block'}}/>
                      {fields.dosNsFile.src &&
                      <label htmlFor="file-3" className="d-flex file-preview mt-2 align-items-center">
                        <PdfIcon size="xl" className="mr-3"/>
                        <div className="d-block">
                          <Label>{fields.dosNsFile.file.name}</Label>
                          <Label className="d-block actions" size="s">
                            <label htmlFor="file-3" style={{cursor: 'pointer', textDecoration: 'underline'}}>Изменить</label>
                          </Label>
                        </div>
                      </label>}
                  </div>
                  <FormField className="mt-3 pb-0">
                    <CheckBox checked={fields.isUSA}
                              onChange={(v) => this.handleChange('isUSA', v) }
                              text="Я являюсь налогоплательщиком США"
                    />
                  </FormField>
                </li>
              )}
              { fields.isUSA && (
                <li className="mb-2 px-3 col-md-12">
                  <div className="file-block mb-2">
                    <Label size="s" className="d-inline-block mb-2">Документ, который нужен для налогоплательщиков США <Link pseudo size="s" className="opacity-high ml-2"><span style={{textDecoration: 'underline'}}>Подробнее</span></Link></Label>
                    <input type="file" id="file-3" name="uploadingFiles" accept="application/pdf" onChange={e => this.handleFileChange('dosNsUsaFile', e)} style={{display: fields.dosNsUsaFile.file ? 'none' : 'block'}}/>
                    {fields.dosNsUsaFile.src &&
                    <label htmlFor="file-3" className="d-flex file-preview mt-2 align-items-center">
                      <PdfIcon size="xl" className="mr-3"/>
                      <div className="d-block">
                        <Label>{fields.dosNsUsaFile.file.name}</Label>
                        <Label className="d-block actions" size="s">
                          <label htmlFor="file-3" style={{cursor: 'pointer', textDecoration: 'underline'}}>Изменить</label>
                        </Label>
                      </div>
                    </label>}
                  </div>
                </li>
              )}
            </ul>
          </FormField>
          { (status==='clean' || status==='error') && <Button type="submit">Загрузить файлы</Button> }
          { status==='loading'  && <Label><Spin visible size="m" className="mr-3"/> Загружаем данные... Пожалуйста подождите, это может занять несколько минут</Label>}
          { status==='error'  && <Label className=" ml-3 color-danger">{err.debugMessage}</Label>}
        </div>
      </Form>
    )
  }
  
  handleChange(field,val) {
    console.log({field, val});
    const newState = {...this.state};
    newState.fields[field]  = val;
    this.setState(newState);
  }
  
  handleFileChange(field, e) {
    const reader = new FileReader();
    const file = e.target.files[0];
    if(!file) return;
    reader.onload = () => {
      this.handleChange(field, {file, src: reader.result});
    };
    reader.readAsDataURL(file);
  }
  
  handleSubmit(e) {
    e.preventDefault();
    const {fields} = this.state;
    const formData = new FormData();
    let uploadType = DOS_STANDARD;
    formData.append('iin', fields.iin);
    if (fields.type === full_type) {
      formData.append('uploadingFiles', fields.frontPage.file);
      formData.append('udlType', full_type);
    } else {
      formData.append('uploadingFiles', fields.frontPage.file);
      formData.append('uploadingFiles', fields.backPage.file);
      formData.append('udlType', single_type);
    }
    if (!fields.isNS) {
      formData.append('dosNsFile', fields.dosNsFile.file);
      uploadType = DOS_NS;
    }
    if (!fields.isNS && fields.isUSA) {
      formData.append('dosNsUsaFile', fields.dosNsUsaFile.file);
      uploadType = DOS_NS_USA;
    }
    formData.append('uploadType', uploadType);
    this.setState({err: null, status: 'loading'});
    addCompanyService.sendFiles(formData).then(({data}) => {
      console.log(data);
      alert(data.isValid ? 'Валидно' : 'Не валидно');
      
      addCompanyService.saveFiles({
        firstName: 'Данияр',
        lastName: 'Жадырасын',
        iin: this.state.fields.iin,
        uploadType,
      }).then(res => {
        console.log(res);
      });
      
      this.setState({status: 'success'});
    }).catch(err => {
      console.error(err);
      if (err.response && err.response.data) err = err.response.data;
      this.setState({err, status: 'error'});
    });
  }
}

export default SendDocumentsForm;
