import React from 'react'
import Form from 'arui-feather/form'
import Heading from 'arui-feather/heading'
import FormField from 'arui-feather/form-field'
import Input from 'arui-feather/input'
import Select from 'arui-feather/select'
import CalendarInput from 'arui-feather/calendar-input'
import Button from 'arui-feather/button'
import Paragraph from "arui-feather/paragraph";

const types_of_registration_document = [
  {text: 'Справка о государственной регистрации ЮЛ', value: '1'},
  {text: 'Журнал учета личных дел', value: '2'},
  {text: 'Журнал регистрации вводного инструктажа', value: '3'},
  {text: 'Формы статистической отчетности', value: '4'},
];

class AddDocumentForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        type_of_registration_document: {
          value: '',
        },
        series: {
          value: '',
        },
        number: {
          value: '',
        },
        registration_number: {
          value: '',
        },
        registration_date: {
          value: null,
        },
        date_of_issue: {
          value: null,
        },
        expire_date: {
          value: null,
        },
        organization: {
          value: "",
        },
        INFS: {
          value: "",
        },
        license: {
          value: "",
        },
        types_of_licensed_activities: {
          value: "",
        },
      },
    };

    this.handleChange = this.handleChange.bind(this);
  }
  render() {
    const {fields} = this.state;
    const {onCancel = () => {}} = this.props;
    return (
      <Form className="mb-0 pb-0">
        <Paragraph className="my-3">
          <span className="red-star mx-2"/> - Поля отмеченные красной звездочкой обязательны для заполнения
        </Paragraph>
        <Heading size="s" className="mt-5">
          Регистрационный документ
        </Heading>
        <div className="row">
          <div className="col-md-6">
            <FormField>
              <Select label="Вид регистрационного документа"
                      width="available"
                      mode="radio"
                      className="input-required"
                      value={fields.type_of_registration_document.value ? [fields.type_of_registration_document.value] : null}
                      options={types_of_registration_document}
                      onChange={(v) => this.handleChange('type_of_registration_document', v[0])}
              />
            </FormField>
            <FormField>
              <Input label="Серия"
                     width="available"
                     value={fields.series.value}
                     onChange={(v) => this.handleChange('series', v)}
              />
            </FormField>
            <FormField>
              <Input label="Номер"
                     width="available"
                     value={fields.number.value}
                     onChange={(v) => this.handleChange('number', v)}
              />
            </FormField>
            <FormField>
              <Input label="Регистрационный номер"
                     width="available"
                     value={fields.registration_number.value}
                     onChange={(v) => this.handleChange('registration_number', v)}
              />
            </FormField>
          </div>
          <div className="col-md-6">
            <FormField>
              <CalendarInput label="Дата регистрации"
                             width="available"
                             value={fields.registration_date.value}
                             onChange={(v) => this.handleChange('registration_date', v)}
              />
            </FormField>
            <FormField>
              <CalendarInput label="Дата выдачи"
                             width="available"
                             className="input-required"
                             value={fields.date_of_issue.value}
                             onChange={(v) => this.handleChange('date_of_issue', v)}
              />
            </FormField>
            <FormField>
              <CalendarInput label="Срок действия"
                             width="available"
                             value={fields.expire_date.value}
                             onChange={(v) => this.handleChange('expire_date', v)}
              />
            </FormField>
            <FormField>
              <Input label="Организация"
                     width="available"
                     className="input-required"
                     value={fields.organization.value}
                     onChange={(v) => this.handleChange('organization', v)}
              />
            </FormField>
            <FormField>
              <Input label="ИНФС"
                     width="available"
                     value={fields.INFS.value}
                     onChange={(v) => this.handleChange('INFS', v)}
              />
            </FormField>
          </div>
        </div>
        <Heading size="s" className="mt-4">
          Сведения о лицензируемой деятельности
        </Heading>
        <div className="row">
          <div className="col-md-6">
            <FormField>
              <Input label="Лицензия"
                     width="available"
                     value={fields.license.value}
                     onChange={(v) => this.handleChange('license', v)}
              />
            </FormField>
            <FormField>
              <Input label="Виды лицензируемой деятельности"
                     width="available"
                     value={fields.types_of_licensed_activities.value}
                     onChange={(v) => this.handleChange('types_of_licensed_activities', v)}
              />
            </FormField>
          </div>
        </div>
        <div className="d-flex justify-content-end">
          <Button view="action" type="button" onClick={onCancel}>Отменить</Button>
          <Button view="extra" type="submit">Сохранить</Button>
        </div>
      </Form>
    )
  }
  handleChange(field, val) {
    console.log({field, val});
    const newState = {...this.state};
    newState.fields[field].value  = val;
    this.setState(newState);
  }
  
}

export default AddDocumentForm;
