import React from 'react'
import Button from 'arui-feather/button'
import {Table} from "element-react"
import IconButton from 'arui-feather/icon-button'
import TrashIcon from 'arui-feather/icon/action/delete'
import EditIcon from 'arui-feather/icon/action/edit'
import CheckBox from 'arui-feather/checkbox'
import Heading from 'arui-feather/heading'
import Modal from 'arui-private/modal'
import AddContactForm from './AddContactForm'

class CompanyContactsTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      default_type: 0,
      columns: [
        {
          label: 'Основной',
          render: (row) => (
            <CheckBox onChange={() => this.setState({default_type: row.id})} checked={this.state.default_type===row.id}/>
          ),
          width: 120,

        },
        {
          label: "Тип контакта",
          prop: "contact_type",
        },
        {
          label: "Вид контакта",
          prop: "contact_view",
        },
        {
          label: "Описание",
          prop: "description",
        },
        {
          label: "Контактное лицо",
          prop: "contact_person",
        },
        {
          label: "Действия",
          prop: "actions",
          width: 120,
          render: (row, col, index) => index>1 ? (
            <div className="d-flex align-items-center" style={{height: '60px'}}>
              <div className="button-accent"><IconButton onClick={() => this.removeFromList(index)} icon={<TrashIcon size="m"/>}/></div>
              <div className="button-accent"><IconButton className="mx-1" icon={<EditIcon size="m"/>}/></div>
            </div>
          ) : ''
        },
      ],
      list: [
        {
          id: 0,
          contact_type: 'Телефон',
          contact_view: 'Мобильный',
          description: '+7(701)782-44-11',
        },
        {
          id: 1,
          contact_type: 'Email',
          contact_view: 'Раб.(внеш)',
          description: 'ba@mail.ru',
        },
        {
          id: 2,
          contact_type: 'Телефон',
          contact_view: 'Домашний',
          description: '+7(727)245-44-11',
          contact_person: 'Иванов И.И.'
        },
      ],
      modal_visible: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  render() {
    const {columns, list, modal_visible} = this.state;
    return (
      <div>
        <div className="d-flex justify-content-end my-4">
          <Button size="s" onClick={() => this.setState({modal_visible: true})}>Добавить</Button>
          <Button view="extra" size="s">Удалить</Button>
        </div>
        <Table className="w-100"
               columns={columns}
               data={list}
               emptyText="Список контактов пуст, нажмите на кнопку «Добавить»"
               border/>
        <Modal visible={modal_visible}
               hasCloser
               autoclosable
               className="modal-s mb-5"
               onClose={() => this.setState({modal_visible: false})}>
          <Heading className="mt-0 mb-3" size='xs'>
            Контакт
          </Heading>
          <AddContactForm onCancel={() => this.setState({modal_visible: false})} onSubmit={(v) => this.addToList(v)}/>
        </Modal>
        <div className="text-right mt-5">
          <Button view="extra" className="blued" onClick={this.handleSubmit}>Далее</Button>
        </div>
      </div>
    )
  }
  
  addToList(v) {
    const list = [...this.state.list, v];
    this.setState({list, modal_visible: false});
  }
  
  removeFromList(index) {
    const list = [...this.state.list];
    list.splice(index, 1);
    this.setState({list});
  }
  
  handleSubmit(e) {
    if (this.props.onSubmit) {
      this.props.onSubmit(e)
    }
  }
}

export default CompanyContactsTable;
