import React from 'react'
import PersonBasicForm from './PersonBasicForm'
import AddDocumentIPForm from "./AddDocumentIPForm";
import Tabs from "arui-feather/tabs";
import TabItem from "arui-feather/tab-item";
import CompanyAddressForm from "./CompanyAddressForm";
import CompanyContactsTable from "./CompanyContactsTable";

class AddFounderForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'basic'
    };
    this.handleTabChange = this.handleTabChange.bind(this);
  }

  render() {
    const {selectedTab} = this.state;
    return (
      <div className="pb-5">
        <Tabs className="mb-4">
          <TabItem checked={selectedTab==='basic'} onClick={(e) => this.handleTabChange(e,'basic')}>
            Основные данные
          </TabItem>
          <TabItem checked={selectedTab==='documents'} onClick={(e) => this.handleTabChange(e,'documents')}>
            Документы
          </TabItem>
          <TabItem checked={selectedTab==='address'} onClick={(e) => this.handleTabChange(e,'address')}>
            Адрес
          </TabItem>
          <TabItem checked={selectedTab==='contacts'} onClick={(e) => this.handleTabChange(e,'contacts')}>
            Контакты
          </TabItem>
        </Tabs>
        { selectedTab==='basic' && <PersonBasicForm/> }
        { selectedTab==='documents' && <AddDocumentIPForm/> }
        { selectedTab==='address' && <CompanyAddressForm/> }
        { selectedTab==='contacts' && <CompanyContactsTable/> }
      </div>
    )
  }

  handleTabChange(e, v) {
    e.preventDefault();
    this.setState({selectedTab: v})
  }

}

export default AddFounderForm;
